.s-events-cities {
    padding: rem(20) 0;

    @media (min-width: 768px) {
        padding: rem(35) 0;
    }

    @media (min-width: 1024px) {
        padding: rem(50) 0;
    }

    &__items {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: rem(15);
        flex-wrap: wrap;

        @media (min-width: 1024px) {
            gap: rem(19);
            align-items: flex-start;
        }

        @media (min-width: 1920px) {
            gap: rem(40);
        }
    }

    &__item {
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        height: rem(24);
        padding: 0 rem(12);
        font-size: rem(12);
        font-weight: 300;
        border: rem(1) solid #ffffff;
        border-radius: rem(5);
        transition: all 0.4s ease;

        &:hover {
            border: rem(1) solid #d91745;
        }
    }
}
